import React from 'react'
// tslint:disable-next-line: no-implicit-dependencies
import { BackgroundColorProperty } from 'csstype'
import {
  Card,
  CardHeader,
  CardHeaderIconWrapper,
  CardHeaderText,
  CardBody,
} from './Card'
import { Flex } from './flex'
import { DynamicIcon } from './DynamicIcon'
import styled, { StyledComponent } from 'styled-components'

interface IExperienceCardProps {
  name: string
  type: string
  icon: string
  dateRange: string
  description: string
  backgroundColor: BackgroundColorProperty
}

const ExperienceCardHeader: StyledComponent<
  'header',
  any,
  {},
  never
> = styled.header`
  padding: 2em 2em 0em 2em;

  h2 {
    margin: 0;
  }
`

const ExperienceCardHeaderText: StyledComponent<
  'div',
  any,
  {},
  never
> = styled.div`
  padding: 0 2em;
`

export const ExperienceCard: ({
  name,
  type,
  icon,
  dateRange,
  description,
  backgroundColor,
}: IExperienceCardProps) => JSX.Element = ({
  name,
  type,
  icon,
  dateRange,
  description,
  backgroundColor,
}: IExperienceCardProps): JSX.Element => (
  <Card>
    <ExperienceCardHeader>
      <Flex justifyContent="flex-start">
        <CardHeaderIconWrapper backgroundColor={backgroundColor}>
          <DynamicIcon path={icon} />
        </CardHeaderIconWrapper>
        <ExperienceCardHeaderText>
          <h1>
            {name} ({type})
          </h1>
          <h2>{dateRange}</h2>
        </ExperienceCardHeaderText>
      </Flex>
    </ExperienceCardHeader>
    <CardBody>
      <main>
        <p>{description}</p>
      </main>
    </CardBody>
  </Card>
)
